:root {
  --color-primary-blue: #1125d4e6;
  --color-light-red: #ff57571a;
  --color-orangey-yellow: #ffb01f1a;
  --color-green-teal: #00bd911a;
  --color-neutral-white: #fff;
  --color-neutral-pale-blue: #ebf1ff;
  --color-neutral-light-lavender: #c8c7ff33;
  --color-neutral-dark-gray: #303b5a;
  --gradient-primary: linear-gradient(to bottom, #7857ff, #2e2be9);
  --gradient-secondary: linear-gradient(to bottom, #4e21ca, #2421ca00);
}

* {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
}

html {
  box-sizing: border-box;
  font-size: 62.5%;
}

body {
  justify-content: center;
  align-items: center;
  height: 100vh;
  font-family: Hanken Grotesk, sans-serif;
  font-weight: 500;
  display: flex;
}

.app {
  max-width: 100%;
  height: 100vh;
}

@media (width >= 430px) {
  .app {
    max-width: 38rem;
    height: 100vh;
    box-shadow: 0 0 20px #00000026;
  }
}

@media (width >= 900px) {
  .app {
    align-items: space-around;
    border-radius: 25px;
    justify-content: center;
    max-width: 60rem;
    height: auto;
    margin: auto;
    display: flex;
  }
}

p {
  font-size: 1.8rem;
}

@media (width >= 900px) {
  p {
    font-size: 1.6rem;
  }
}

.result {
  background: var(--gradient-primary);
  color: #c8c7ffca;
  border-radius: 0 0 1.4rem 1.4rem;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  gap: 1.5rem;
  padding: 3rem;
  display: flex;
}

.result--heading {
  font-size: 2rem;
  font-weight: 700;
}

.score {
  background: var(--gradient-secondary);
  border-radius: 50%;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  width: 12rem;
  height: 12rem;
  padding: 2rem;
  display: flex;
}

.score--user {
  color: var(--color-neutral-white);
  font-size: 4rem;
  font-weight: 800;
}

.score--total {
  font-size: 1.2rem;
  font-weight: 700;
}

.result--grade {
  color: var(--color-neutral-white);
  font-size: 2rem;
}

.comment {
  text-align: center;
  font-size: 1.8rem;
  font-weight: 500;
}

.summary {
  background-color: var(--color-neutral-white);
  margin: 2rem;
}

@media (width >= 900px) {
  :is(.result, .summary) {
    border-radius: 25px;
    width: 50%;
    margin: 0;
    padding: 3rem;
  }
}

.summary--heading {
  margin-bottom: 2rem;
  font-size: 2rem;
  font-weight: 700;
}

ul {
  flex-direction: column;
  gap: 1.5rem;
  margin-bottom: 2rem;
  list-style: none;
  display: flex;
}

.list--item {
  border-radius: .6rem;
  justify-content: space-between;
  padding: 1.5rem;
  font-weight: 700;
  display: flex;
}

.list--item:first-child {
  background-color: var(--color-light-red);
  color: #ff5757e6;
}

.list--item:nth-child(2) {
  background-color: var(--color-orangey-yellow);
  color: #ffb01fe6;
}

.list--item:nth-child(3) {
  background-color: var(--color-green-teal);
  color: #00bd91e6;
}

.list--item:nth-child(4) {
  background-color: var(--color-neutral-light-lavender);
  color: var(--color-primary-blue);
}

.list--result {
  color: var(--color-neutral-dark-gray);
  font-weight: 800;
}

.list--category {
  display: flex;
}

.icon {
  margin-right: .8rem;
}

span {
  color: #303b5a40;
  font-weight: 800;
}

.btn {
  background-color: var(--color-neutral-dark-gray);
  color: var(--color-neutral-white);
  cursor: pointer;
  border: none;
  border-radius: 4rem;
  width: 100%;
  padding: 1.5rem 3rem;
  font-size: 1.8rem;
  font-weight: 500;
}

@media (width >= 900px) {
  :is(.btn:hover, .btn:active) {
    background: var(--gradient-primary);
    transition: all .2s;
  }
}

